            font-family: 'MoglanDemo';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 21px;
            color: #FFFFFF;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 8px 8px 8px 12px;
            gap: 10px;
            width:fit-content;
            height:fit-content;
            border: 1px solid #FFFFFF;
            border-radius: 52px;
        }
        .socialCon{
            height: 127px;
            width: 176px;
            gap:8px;
        }
        .menu_footer{
            padding-bottom: 25px;
            display: flex;
            justify-content: space-between;
            padding-left:20px;
            padding-right:25px;
            align-items: center;
        }
        .menu_footer-innerCon{
            align-self:flex-end;
        }
        .menu_footer-p{
            width: 221.5px;
            height: 21px;
            font-family: 'Varela';
            font-style: normal;
            font-weight: 400;
            font-size: 8px;
            line-height: 21px;
            display: flex;
            align-items: flex-end;
            text-transform: uppercase;
            color: #FFFFFF;
            text-align: start;
        }
        .footer_p-hr{
            width:8px;
            height: 1px;
            background-color: #FFFFFF;
            margin:0px 12.5px;
            border: none;
            display:inline;
            transform: translateY(-10px);
        }
